var visit = require('unist-util-visit');

module.exports = createPlugin;

function createPlugin(displayName, createRenderer) {
  attacher.displayName = displayName;
  return attacher;

  function attacher(options) {
    var renderer = createRenderer(options);
    transform.displayName = displayName + 'Transform';
    return transform;

    function transform(tree) {
      var context = tree;
      var found = false;
      visit(tree, 'element', onelement);

      if (found && renderer.styleSheet) {
        context.children.push(renderer.styleSheet());
      }

      function onelement(node) {
        var classes = node.properties.className || [];
        var inline = classes.includes('math-inline');
        var display = classes.includes('math-display');

        if (node.tagName === 'head') {
          context = node;
        }

        if (!inline && !display) {
          return;
        }

        found = true;
        renderer.render(node, {
          display: display
        });
        return visit.SKIP;
      }
    }
  }
}