var createPlugin = require('./lib/core');

module.exports = createPlugin('rehypeMathJaxBrowser', renderBrowser);

function renderBrowser(options) {
  var settings = options || {};
  var display = settings.displayMath || ['\\[', '\\]'];
  var inline = settings.inlineMath || ['\\(', '\\)'];
  return {
    render: render
  };

  function render(node, renderOptions) {
    var delimiters = renderOptions.display ? display : inline;
    node.children.unshift({
      type: 'text',
      value: delimiters[0]
    });
    node.children.push({
      type: 'text',
      value: delimiters[1]
    });
  }
}